import React, {useEffect} from 'react';
import Paper from '@mui/material/Paper';
import MenuItem from '@mui/material/MenuItem';
import MenuList from '@mui/material/MenuList';
import { useLocation } from "react-router-dom";

import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../store/rootReducer';

import AdminMenuItem from "./Item";


const AdminMenu: React.FC= () => {
    const user = useSelector((state: RootState) => state.user);

    return (
        <Paper>
            <MenuList>
                <AdminMenuItem path={'/admin'} text={''} />
                <hr />
                <AdminMenuItem path={'/admin/users'} text={''} />
                <AdminMenuItem path={'/admin/services'} text={' '} />
                <AdminMenuItem path={'/admin/orders'} text={''} />
                <AdminMenuItem path={'/admin/photos'} text={''} />
                <hr />
                <AdminMenuItem path={'/admin/papers'} text={''} />
                <AdminMenuItem path={'/admin/sizes'} text={''} />
                <AdminMenuItem path={'/admin/types'} text={' '} />
                <hr />
                <AdminMenuItem path={'/'} text={' '} />
            </MenuList>
        </Paper>
    )
}

export default AdminMenu;